
@media screen and (min-width : 1024px) {

    .technology {
        transition: opacity 1s ease-out;
        width: 100vw;
        display: flex;
        justify-content: center;
        gap: 5vw !important;
    }


    .technology img{
        position: relative;
        max-width: 450px;
        bottom: 7vh ;
    }

    .technology .info {
        max-width: 35vw;
        padding-top: 10vh;
        display: flex;
        flex-direction: column; 
        gap: 24px;
    }

    .technology .info div{
        display: flex;
        flex-direction: column;
        gap: 25px;
    }

    .technology .info div span {
        font-size: 1.5em;
        font-family: 'Barlow Condensed';
        font-weight: unset;
        color: #D0D6F9;
        letter-spacing: 4.72px;
    }

    .technology .info div h1 {
        text-transform: uppercase;
        font-size: 3em;
        font-weight: 400;
        font-family: 'Bellefair', serif;
        color: white;
    }

    .technology .info p {
        font-size: 1em;
        font-family: 'Barlow';
        color: #D0D6F9;
        line-height: 27px;
    }

}
