
@media screen and (min-width : 1024px) {

    header.top {
        padding: 5vh 0 0 5vw ;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    header.top img {
        max-height: 48px;
    }

    div.line{
        position: relative;
        left: 8vw;
        z-index: 2;
        height: 1px;
        width: 30vw;
        fill: white;
        background-color: #979797;
    }

    nav.navbar{
        min-width: 50vw;
        padding: 0 30px 0 15vw;
        border-radius: 3px;
        backdrop-filter: blur(4px);
        background-color: #ffffff0d;
        height: 90px;
        display: flex;
        align-items: center;
    }

    nav.navbar ul.links{
        height: 100%;
        display: flex;
        align-items: center;
        gap: 30px;
    }

    ul.links li {
        transition: 0.7s;
        height: 100%;
    }

    ul.links li:hover {
        border-bottom: 3px solid white;
    }

    ul.links li a{
        height: 100%;
        display: flex;
        align-items: center;
        gap: 10px;
    }

    ul.links li span{
        font-family: 'Barlow Condensed', sans-serif;
        font-weight: 700;
        font-size: 1em;
        color: white;
        letter-spacing: 2.7px;
    }

    ul.links li p{
        font-family: 'Barlow Condensed', sans-serif;
        font-weight: 400;
        font-size: 1em;
        color: white;
        letter-spacing: 2px;
    }
}
