@import url('https://fonts.googleapis.com/css2?family=Barlow&family=Barlow+Condensed:wght@400;700&family=Bellefair&display=swap');

 
*{
  padding: 0;
  margin: 0;
  outline: none;
  list-style: none;
  text-decoration: none;
  z-index: 1;
  border: 0;
  
}

html , body , #root{
  min-width: 100vw;
  min-height: 100vh;
}



@media screen and (min-width : 1024px) {
  body{
    overflow: hidden;
  }

  .mobile {
    display: none;
  }

  html , body , #root{
    max-width: 100vw;
    height: 100vh;
    background-color: rgba(57, 57, 57, 0.829);
  }
  body {
    overflow: hidden;
  }
}
