@media screen and (min-width : 1024px) {
   
    .destination{
        padding: 7vh 10vw 0 10vw ;
        display: flex;
        justify-content: space-between;
        width: 80vw;
    }

    .destination .targetImg{
        display: flex;
        flex-direction: column;
        gap: 60px;
        align-items: center;
    }

    .destination .targetImg div {
        display: flex;
        gap: 20px;
    }

    .destination .targetImg img{
        transition: 3s;
    }

    .destination .targetImg div h2, .destination .targetImg div span{
        font-size: 1.75em;
        color: white;
        font-family: 'Barlow Condensed';
        font-weight: 400;
        letter-spacing: 4.72px;

    }

    .destination .targetImg div span{
        color: #979797;
    }

    .destination .info {
        max-width: 33vw;
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 30px;
    }

    .destination .info header.targets nav.choose {
        display: flex;
        justify-content: flex-start;
        height: 60px;
        gap: 50px;
    }

    .destination .info header.targets nav.choose button {
        background-color: transparent;
        font-size: 1em;
        color: #D0D6F9;
        letter-spacing: 2.7px;
        font-family: 'Barlow Condensed';
        cursor: pointer;
    }

    .destination .info h1{
        font-family: 'Bellefair';
        font-size: 6.5em;
        font-weight: unset;
        text-transform: uppercase;
        color: white;
        
    }

    .destination .info p.description{
        font-size: 1.1em;
        font-family: 'Barlow';
        color: #D0D6F9;
        line-height: 32px;
    }

    .destination .travelInfo{
        border-top: 1px solid hsl(231 15% 26%);
        padding-top: 30px;
        display: flex;
        gap: 100px;
    }

    .destination .travelInfo div {
        display: flex;
        flex-direction: column;
        gap: 5px;
    }

    .destination .travelInfo div p{
        font-size: 1.75em;
        font-family: 'Bellefair';
        color: white;

    }

    .destination .travelInfo div span{
        font-size: 0.9em;
        font-family: 'Barlow';
        color: #D0D6F9;
        line-height: 32px;
    }

}
