
#root:has(div.explore){
    background-image: url(../../assets/home/background-home-desktop.jpg);
    background-size: cover;
}


@media screen and (min-width : 1024px) {

    .explore{
        padding: 25vh 15vw 0 15vw ;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .description{
        max-width: 30vw;
        display: flex;
        flex-direction: column;
        gap: 24px;
    }

    .explore button {
        height: 274px;
        width: 274px;
        border-radius: 50%;
        font-size: 2em;
        font-family: 'Bellefair';
        font-weight: unset;
    }

    .explore button:hover {
        transition: 0.7s;
        background-color: transparent;
        color: white;
        font-size: 3.5em;
    }

    .description h3 {
        font-size: 1.2em;
        font-family: 'Barlow Condensed';
        font-weight: unset;
        color: #D0D6F9;
        letter-spacing: 4.72px;
    }

    .description h1 {
        font-size: 150px;
        font-weight: 400;
        font-family: 'Bellefair', serif;
        color: white;
    }

    .description p {
        position: relative;
        bottom: 20px;
        font-size: 1em;
        font-family: 'Barlow';
        color: #D0D6F9;
        line-height: 32px;
    }

}

