
@media screen and (min-width : 1024px) {

    .member {
        padding: 0 10vw 0 0;
        width: 80vw;
        display: flex;
        justify-content: space-between;
    }


    .member img{
        position: relative;
        bottom: 7vh ;
        max-height : calc(100vh - 128.75px - 10vh);
    }

    .member .info {
        max-width: 30vw;
        padding-top: 10vh;
        display: flex;
        flex-direction: column; 
        gap: 24px;
    }

    .member .info .top{
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    .member .info .top h2 {
        font-size: 2em;
        font-family: 'Barlow Condensed';
        font-weight: unset;
        color: #D0D6F9;
        letter-spacing: 4.72px;
    }

    .member .info .top h1 {
        font-size: 3.5em;
        font-weight: 400;
        font-family: 'Bellefair', serif;
        color: white;
    }

    .member .info p {
        font-size: 1em;
        font-family: 'Barlow';
        color: #D0D6F9;
        line-height: 27px;
    }

    .member .steps{
        padding-top: 5vh;
        display: flex;
        gap: 30px;
    }

    .member .steps div{
        width: 20px;
        height: 20px;
        border-radius: 50%;
        background-color: white;
    }

}
