
@media screen and (min-width : 1024px) {
    #root:has(.technologiesMain){
        background-image: url(../../assets/technology/background-technology-desktop.jpg);
        background-size: cover;
    }

    .technologiesMain{
        padding-top: 7vh;
        padding-left: 10vw;
        transition: 2s;
        position: relative;
        width: max-content;
        display: flex;
        flex-direction: column;
        gap: 40px;
    }

    .mobile {
        display: none;
    }

    .technologiesMain header.title{
        position: relative;
        z-index: 1000;
        display: flex;
        gap: 20px;
    }

    .technologiesMain header.title h2, .technologiesMain header.title span{
        font-size: 1.75em;
        color: white;
        font-family: 'Barlow Condensed';
        font-weight: 400;
        letter-spacing: 4.72px;
    }

    .technologiesMain header.title span{
        color: #979797;
    }

    .technologiesMain .techonlogies{
        transition: 2s;
        position: relative;
        display: flex;
        gap: 10vw;
        width: max-content;
    }

    .technologies {
        display: flex;
        flex-direction: row-reverse;
        gap: 5vw;
    }

    .technologies .choose ul{
        display: flex;
        flex-direction: column;
        gap: 40px; 
    }

    .technologies div {
        transition: 2s;
        position: relative;
        display: flex;
        gap: 26vw;
        width: max-content;
    }

    .technologies .choose ul li{
        width: 80px;
        height: 80px;
        font-size: 2em;
        font-family: "Bellefair";
        background-color: transparent;
        color: white;
        border: 1.5px solid white;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;

    }

}

