
@media screen and (min-width : 1024px) {
    #root:has(.crew){
        background-image: url(../../assets/crew/background-crew-desktop.jpg);
        background-size: cover;
    }

    .crew{
        padding-top: 7vh;
        padding-left: 15vw;
        transition: 2s;
        position: relative;
        width: max-content;
        display: flex;
        flex-direction: column;
        gap: 40px;
    }

    .crew .title{
        display: flex;
        gap: 20px;
    }

    .crew .title h2, .crew .title span{
        font-size: 1.75em;
        color: white;
        font-family: 'Barlow Condensed';
        font-weight: 400;
        letter-spacing: 4.72px;

    }

    .crew .title span{
        color: #979797;
    }

    .crew .members{
        transition: 2s;
        position: relative;
        display: flex;
        gap: 10vw;
        width: max-content;
    }

}
